/* general styles */

body {
    font-family: "Raleway", sans-serif !important;
  }
  
  ul {
    list-style-type: none;
  }
  
  a {
    text-decoration: none !important;
  }
  
  h1 {
    color: #0563bb;
    font-weight: bold;
  }
  
  p {
    font-size: 20px;
    color: #484848;
  }
  
  .section {
    text-align: center;
  }
  
  .skillbar {
    cursor: pointer;
  }
  
  .hide {
    display: none;
  }
  
  .display {
    display: block;
  }
  
  /* flash alert styles */
  
  .flash-container {
    width: 100%;
    position: fixed;
    z-index: 700;
    padding-top: 20px;
    font-weight: bold;
  }
  
  .alert {
    text-align: center !important;
    width: 500px !important;
    margin-left: auto !important;
    margin-right: auto !important;
    color: #211809 !important;
  }
  
  /* Nav Screen */
  
  .nav-screen {
    height: 100%;
    z-index: 500;
    position: fixed;
    background-color: #000;
    opacity: 1;
    right: -285px; /* start off behind the scenes */
    -webkit-font-smoothing: antialiased;
    position: fixed;
    width: 285px;
  }
  
  .nav-screen .active {
    display: block;
  }
  
  /* nav header links */
  
  .nav-brand {
    position: fixed;
    left: 0;
    z-index: 600;
    padding: 10px;
  }
  
  .nav-brand img {
    width: 50px;
  }
  
  .fa-bars {
    display: none !important;
    color: #0563bb;
    float: right;
    padding: 20px;
    position: fixed;
    right: 0;
    z-index: 500;
    cursor: pointer;
  }
  
  .fa-times {
    color: #0563bb;
    float: right;
    padding: 20px;
    right: 0;
    z-index: 600;
    cursor: pointer;
  }
  
  .header-links {
    position: fixed;
    width: 100%;
    z-index: 500;
  }
  
  .header-links li {
    color: white;
    display: inline;
    float: right;
    padding: 15px;
    font-size: 16px;
  }
  
  .header-links a {
    color: #757575;
    -webkit-transition: all 0.2s ease-in;
  }
  
  .header-links a:hover {
    color: #ff6363 !important;
  }
  
  /* slide out nav menu styles */
  
  .nav-container {
    padding-top: 10%;
  }
  
  .nav-links {
    width: 250px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }
  
  .nav-links ul {
    margin-top: 15%;
    text-align: center;
    padding-top: 10%;
  }
  
  .nav-links a {
    color: #ffffff;
    font-size: 30px;
    line-height: 2.5;
    font-weight: bold;
    text-decoration: none;
    margin-left: auto;
    margin-right: auto;
  }
  
  .nav-links a:hover {
    color: #ff6363;
    transition: all 0.6s ease-in-out;
  }
  
  #fp-nav ul li .fp-tooltip {
    color: #5f5f5f !important;
  }
  
  /* Expanding border effect */
  
  .header-links a::after {
    display: block;
    margin: 0 auto;
    margin-top: 5px;
    width: 0;
    height: 2px;
    background-color: #fff;
    content: "";
    opacity: 0;
    transition: width 0.6s, opacity 0.8s;
  }
  
  .header-links a:hover::after,
  .header-links a:focus::after {
    opacity: 1;
    width: 100%;
  }
  
  /* main header section */
  .ff h1{
    margin-top: 50%;
    color: #0563bb;
}
  
  .opaque-bg {
    margin-left: auto;
    margin-right: auto;
    max-width: 380px;
    padding: 1px 1px 5px 5px;
    /* vertical-align: middle; */
  }
  
  .aboutme {
    background: url("https://img.freepik.com/free-photo/green-wall-mockup-with-green-plant-shelf3d-rendering_41470-4114.jpg?w=1060")
      no-repeat;
    background-size: cover;
  }
  
  .aboutme p {
    color: white;
    font-weight: 100;
    font-size: 17.4px;
  }
  
  /* bouncing arrow */
  
  .fa-chevron-down {
    color: #0563bb !important;
    bottom: 10px;
    margin-left: -30px;
    cursor: pointer;
    position: absolute;
    opacity: 0;
    -webkit-transition: 1.2s ease;
  }
  
  /* Scroll down indicator (bouncing) */
  
  @keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
  }
  
  .bounce {
    animation: bounce 2s infinite;
  }
  
  /* about section */
  
  /* Skill bars */
  
  .container-skillbar {
    width: 100%;
    padding-top: 30px;
    padding-right: 2%;
    padding-left: 2%;
    height: auto;
    overflow: none;
  }
  .skillbar {
    position: relative;
    display: block;
    margin-bottom: 15px;
    width: 100%;
    background: #efefef;
    height: 30px;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    transition: 0.4s linear;
    transition-property: width, background-color;
  }
  
  .skillbar-title {
    position: absolute;
    top: 0;
    left: 0;
    width: 110px;
    font-weight: bold;
    font-size: 13px;
    color: #fff;
    background: #6adcfa;
    -webkit-border-top-left-radius: 3px;
    -webkit-border-bottom-left-radius: 4px;
    -moz-border-radius-topleft: 3px;
    -moz-border-radius-bottomleft: 3px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  
  .skillbar-title span {
    display: block;
    background: rgba(0, 0, 0, 0.15);
    padding: 0 20px;
    height: 30px;
    line-height: 30px;
    -webkit-border-top-left-radius: 3px;
    -webkit-border-bottom-left-radius: 3px;
    -moz-border-radius-topleft: 3px;
    -moz-border-radius-bottomleft: 3px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  
  .skillbar-bar {
    height: 30px;
    width: 0px;
    border-radius: 3px;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
  }
  
  .skill-bar-percent {
    position: absolute;
    right: 10px;
    top: 0;
    font-size: 11px;
    height: 30px;
    line-height: 30px;
    color: #ffffff;
    color: rgba(0, 0, 0, 0.5);
  }
  
  /* portfolio section */
  
  .content {
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
  }
  
  .content-slide {
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
  }
  
  .content-slide img {
    width: 70%;
  }
  
  .fp-controlArrow.fp-next {
    border-color: transparent transparent transparent #ff6363 !important;
    right: 45px !important;
  }
  
  .fp-controlArrow.fp-prev {
    border-color: transparent #ff6363 transparent transparent !important;
    left: 45px !important;
  }
  
  #fp-nav ul li a span,
  .fp-slidesNav ul li a span {
    background: rgba(0, 0, 0, 0.62) !important;
  }
  
  /* contact us styles */
  
  .fa-paper-plane {
    color: #484848;
  }
  
  /* paper plane shake on hover */
  
  .plane-animated {
    animation-duration: 6s;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-play-state: paused;
  }
  
  .fa-paper-plane:hover {
    /* Toggle our animation play state to running when we are hovering over our sticker */
    animation-play-state: running;
  }
  
  @keyframes shake {
    0%,
    100% {
      transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translateX(-10px);
    }
    20%,
    40%,
    60%,
    80% {
      transform: translateX(10px);
    }
  }
  .shake {
    animation-name: shake;
  }
  
  /* input field styles */
  
  .contact-form {
    padding: 0px 15px 0px 15px;
  }
  
  .form-control {
    border: 2px solid rgba(0, 0, 0, 0.27);
    width: 100%;
    max-width: 100%;
    height: 3em;
    font-size: 16px;
    padding: 1em;
    margin: 0.5em 0 2em 0;
    font-weight: bold;
  }
  
  textarea {
    min-height: 10em;
  }
  
  label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
  }
  
  #submit {
    outline: none;
    padding: 12px;
    min-width: 200px;
    border-radius: 2px;
    border: 2px solid #0563bb;
    text-align: center;
    font-size: 1em;
    color: #0563bb;
    background-color: white;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  #submit:hover {
    background-color: #0563bb;
    color: white;
    transition: all 0.3s ease-out;
  }
  
  /* footer styles */
  
  .footer {
    background-color: #484848;
    height: 100px;
    padding-top: 10px;
  }
  
  .footer p {
    color: white;
    font-weight: bold;
    font-size: 16px;
  }
  
  .social-links a {
    color: white;
    padding: 0px 5px 0px 5px;
  }
  
  .social-links a:hover {
    color: #ff6363;
    transition: all 0.2s ease-in-out;
  }
  
  /* smaller resolution styles */
  
  @media (min-width: 100px) and (max-width: 500px) {
    .fa-bars {
      display: block !important;
    }
  
    .header-links {
      display: none;
    }
  }
  
  /* mobile styles */
  
  @media (min-width: 100px) and (max-width: 400px) {
    .content {
      max-width: 300px;
    }
  
    .aboutme h1 {
      font-size: 28px;
    }
  }
  
  /* loading screen animation */
  
  .spinner {
    position: fixed;
    width: 100%;
    height: 100%;
    padding: 25%;
    background-color: #2f2f2f;
    text-align: center;
    font-size: 10px;
    z-index: 1000;
  }
  
  .spinner > div {
    background-color: #ff6363;
    height: 100%;
    width: 6px;
    display: inline-block;
    animation: sk-stretchdelay 1.2s infinite ease-in-out;
  }
  
  .spinner .rect2 {
    animation-delay: -1.1s;
  }
  
  .spinner .rect3 {
    animation-delay: -1s;
  }
  
  .spinner .rect4 {
    animation-delay: -0.9s;
  }
  
  .spinner .rect5 {
    animation-delay: -0.8s;
  }
  
  @keyframes sk-stretchdelay {
    0%,
    40%,
    100% {
      transform: scaleY(0.4);
      -webkit-transform: scaleY(0.4);
    }
    20% {
      transform: scaleY(1);
      -webkit-transform: scaleY(1);
    }
  }
  
  .blinking-cursor {
    font-weight: 500;
    margin-left: 4px;
    font-size: 20px;
    color: white !important;
    animation: 1s blink step-end infinite;
  }
